  .cards {
    grid-template-columns: 1fr;
  }
  .flip-card {
    width: 100%;
    height: 2em;
    margin: 0;
  }
 
}


.errorTitulo {
  margin: 100px auto 0 auto;
  color: white;
  font-family: "Encode Sans Semi Condensed", Verdana, sans-serif;
  text-align: center;
}
.errorDescripcion {
  margin: 20px auto 30px auto;
  font-family: "Encode Sans Semi Condensed", Verdana, sans-serif;
  text-align: center;
}
.errorTitulo,
.errorDescripcion { /* Safari */
  transition: opacity 0.5s linear, margin-top 0.5s linear;
}
.loading .errorTitulo,
.loading .errorDescripcion {
  margin-top: 0px;
}
.gears {
  position: relative;
  margin: 0 auto;
  width: auto;
  height: 0;
}
.gear {
  position: relative;
  z-index: 0;
  width: 120px;
  height: 120px;
  margin: 0 auto;
  border-radius: 50%;
  background: white;
}
.gear:before {
  position: absolute;
  left: 5px;
  top: 5px;
  right: 5px;
  bottom: 5px;
  z-index: 2;
  content: "";
  border-radius: 50%;
  background: #1e1e2f;
}
.gear:after {
  position: absolute;
  left: 25px;
  top: 25px;
  z-index: 3;
  content: "";
  width: 70px;
  height: 70px;
  border-radius: 50%;
  border: 5px solid white;
  box-sizing: border-box;
  background: #1e1e2f;
}
.gear.one {
  left: -130px;
}
.gear.two {
  top: -75px;
}
.gear.three {
  top: -235px;
  left: 130px;
}
.gear .bar {
  position: absolute;
  left: -15px;
  top: 50%;
  z-index: 0;
  width: 150px;
  height: 30px;
  margin-top: -15px;
  border-radius: 5px;
  background: white;
}
.gear .bar:before {
  position: absolute;
  left: 5px;
  top: 5px;
  right: 5px;
  bottom: 5px;
  z-index: 1;
  content: "";
  border-radius: 2px;
  background: #1e1e2f;
}
.gear .bar:nth-child(2) {
  transform: rotate(60deg);
  -webkit-transform: rotate(60deg);
}
.gear .bar:nth-child(3) {
  transform: rotate(120deg);
  -webkit-transform: rotate(120deg);
}
@keyframes clockwise {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes anticlockwise {
  0% {
    -webkit-transform: rotate(360deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
  }
}
@keyframes clockwiseError {
  0% {
    -webkit-transform: rotate(0deg);
  }
  20% {
    -webkit-transform: rotate(30deg);
  }
  40% {
    -webkit-transform: rotate(25deg);
  }
  60% {
    -webkit-transform: rotate(30deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
  }
}
@keyframes anticlockwiseErrorStop {
  0% {
    -webkit-transform: rotate(0deg);
  }
  20% {
    -webkit-transform: rotate(-30deg);
  }
  60% {
    -webkit-transform: rotate(-30deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
  }
}
@keyframes anticlockwiseError {
  0% {
    -webkit-transform: rotate(0deg);
  }
  20% {
    -webkit-transform: rotate(-30deg);
  }
  40% {
    -webkit-transform: rotate(-25deg);
  }
  60% {
    -webkit-transform: rotate(-30deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
  }
}
.gear.one {
  animation: anticlockwiseErrorStop 2s linear infinite;
}
.gear.two {
  animation: anticlockwiseError 2s linear infinite;
}
.gear.three {
  animation: clockwiseError 2s linear infinite;
}
.loading .gear.one,
.loading .gear.three {
  animation: clockwise 3s linear infinite;
}
.loading .gear.two {
  animation: anticlockwise 3s linear infinite;
}

@media (max-width: 650px) {
  .loading{
    margin-left: 0;
    padding-left: 0;
  }
}

@media (max-width: 540px) {
  .loading .gear.one{
    visibility: hidden;
  }

  .loading .gear.three{
    visibility: hidden;
  }
}

