/* Bootstrap Form Control Extension */

.form-control,
.border-line {
  background-color: transparent;
  background-image: none;
  border: 1px solid rgb(255, 255, 255);
  border-radius: 25px;
  color: inherit;
  display: block;
  padding: 6px 12px;
  transition: border-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s;
  width: 100%;
}
.form-control:focus,
.border-line:focus {
  border-color: rgb(51, 91, 243);
  background-color:  rgb(66, 66, 105);;
  color: #e6e6e6;
}
.form-control,
.form-control:focus {
  box-shadow: none;
}
.form-control::-webkit-input-placeholder {
  color: #bdbdbd;
}

